/* Попутчики */

.companions {
  background: url('../images/companions-bg-1.png') no-repeat 0 3%,
              url('../images/companions-bg-2.png') no-repeat 85% 3%,
              url('../images/companions-bg-3.png') no-repeat 0 25%,
              #192144;
  padding: 0 25px 35px;
  border-radius: 0 0 20px 20px;
}

.companions__text-wrapper {
  padding: 71px 30px 60px 24px;
}

.cards-item {
  position: relative;
  min-height: 431px;
  margin-bottom: 9px;
}

.cards-item__image {
  display: block;
}

.cards-item__flag-wrapper,
.cards-item__level-image {
  position: absolute;
}

.cards-item__level-image {
  left: 17px;
  top: 17px;
}

.cards-item__flag-wrapper {
  display: flex;
  min-width: 90px;
  left: 50%;
  top: 0;
  transform: translate(-50%, -50%);
}

.cards-item__flag:not(:last-child) {
  margin-right: 20px;
}

.cards-item__about-person {
  display: grid;
  position: absolute;
  padding: 27px 41px 22px;
  background-color: #FFFFFF;
  border-radius: 0 0 10px 10px;
}

.is-online {
  display: flex;
  align-items: center;
  justify-self: center;
  min-width: 150px;
}

.cards-item__name-text {
  margin: 0 0 0 10px;
  font-family: 'Blogger Sans';
  font-size: 21px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  letter-spacing: 0em;
  color: #1D2E5B;
}

.cards-item__hashtag-text {
  font-family: 'Blogger Sans';
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 18px;
  letter-spacing: 0em;
  color: #444444;
  text-align: center;
}

.cards-item__activity-wraper {
  display: flex;
  width: 120px;
  justify-content: space-between;
  justify-self: center;
}
